
@import "rayx-ui/packages/styles/var.scss";
.demo-wrapper {
  .r-row {
    margin-bottom: 10px;
  }
  .r-col {
    &:nth-child(even) {
      .col-block-primary {
        opacity: 0.8;
      }
    }
    .col-block-primary {
      background: $--color-primary;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
    }
  }
}
